.Dashboard-Container {
  background: hsla(287, 57%, 41%, 1);
  background: linear-gradient(360deg, hsla(287, 57%, 41%, 1) 0%, hsla(190, 100%, 82%, 1) 100%);
  background: -moz-linear-gradient(360deg, hsla(287, 57%, 41%, 1) 0%, hsla(190, 100%, 82%, 1) 100%);
  background: -webkit-linear-gradient(360deg, hsla(287, 57%, 41%, 1) 0%, hsla(190, 100%, 82%, 1) 100%);
  filter: progid: DXImageTransform.Microsoft.gradient(startColorstr="#8A2DA4", endColorstr="#A1EFFF", GradientType=1);
  min-height: 100vh;
  max-width: 100vw;
  overflow: hidden;
}

.PlayerRSWP {
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  position: fixed !important;
  bottom: 0;
  z-index: 1000;
  padding: 1% 2%;
}

.loading {
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 4rem;
  font-family: 'Poppins', sans-serif;
  transform: translate(-50%, -50%);
}